import React from 'react'
import './about.css'
import ReactHtmlParser from 'react-html-parser'
import { Icon } from 'antd'
export class About extends React.PureComponent {
    constructor() {
        super()
        this.state = {
            html: ``,
            text: '',
            show: false
        }
    }
    componentDidMount() {
        const start = `
export const renderMyself = props => {
 return(
    <div>
     <p></p>
    /**/ => color:white
    /**/ => text-align:left
    /**/=> padding:10px
     <p></p> /**/
    /*animation */
     <p></P> /*...*
     <p>: code </p> /**/
    /* transform 3d*/
     <p>22</p>/**/
     <p>HTMLCSS3ES6-7-8NodeReact...</p>
    /*(p)*/
    /**/
     <Icon type='redo' onClick={ () => {
       /className/.test(el.className) ? 
       el.className = someClassName : 
       el.className = anotherClassName 
    }}/> /* toggle*/
    </div>
    )
}`
        for (let i = 0; i < start.length; i++) {
            window.debug() && console.log(i, start[i])
            setTimeout(() => {
                if (i === 90) {
                    this.setState({
                        html: `<div ref='_ref' class='_topView1'><p></p></div>`
                    })
                }
                if (i === 123) {
                    this.setState({
                        html: `<div ref='_ref' class='_topView2'><p></p></div>`
                    })
                }
                if (i === 156) {
                    this.setState({
                        html: `<div ref='_ref' class='_topView2 _topView3'><p></p></div>`
                    })
                }
                if (i === 188) {
                    this.setState({
                        html: `<div ref='_ref' class='_topView2 _topView3 _topView4'><p></p></div>`
                    })
                }
                if (i === 211) {
                    if (document.getElementsByClassName('Scrool')[0]) {
                        document.getElementsByClassName('Scrool')[0].style.transform = 'translateX(-50%) translateY(-162px)'
                    }
                    this.setState({
                        html: `<div ref='_ref' class='_topView2 _topView3 _topView4'>
                                <p></p>
                                <p class=''></p>
                            </div>`
                    })
                }
                if (i === 261) {
                    this.setState({
                        html: `<div ref='_ref' class='_topView2 _topView3 _topView4'>
                                <p></p>
                                <p class=''></p>
                                <p class='itemFromRight'></P>
                            </div>`
                    })
                }
                //
                if (i === 278) {
                    this.setState({
                        html: `<div ref='_ref' class='_topView2 _topView3 _topView4'>
                                <p></p>
                                <p class=''></p>
                                <p style='text-decoration:line-through' class='itemFromRight'></P>
                            </div>`
                    })
                }
                if (i === 309) {
                    this.setState({
                        html: `<div ref='_ref' class='_topView2 _topView3 _topView4'>
                                <p></p>
                                <p></p>
                                <p class='itemFromRight' style='text-decoration:line-through'></P>
                                <p class='itemFromRight'>code</P>
                            </div>`
                    })
                }
                if (i === 340) {
                    this.setState({
                        html: `<div ref='_ref' class='_topViewRotate _topView2 _topView3 _topView4'>
                                <p></p>
                                <p></p>
                                <p class='itemFromRight' style='text-decoration:line-through'></P>
                                <p class='itemFromRight'>code</P>
                            </div>`
                    })
                }
                if (i === 367) {
                    this.setState({
                        html: `<div ref='_ref' class='_topViewRotate _topView2 _topView3 _topView4'>
                                <p></p>
                                <p></p>
                                <p class='itemFromRight' style='text-decoration:line-through'></P>
                                <p class='itemFromRight'>code</P>
                                <p class='itemFromRight'>22</P>
                            </div>`
                    })
                }
                if (i === 441) {
                    this.setState({
                        html: `<div ref='_ref' class='_topViewRotate _topView2 _topView3 _topView4'>
                                <p></p>
                                <p></p>
                                <p class='itemFromRight' style='text-decoration:line-through'></P>
                                <p class='itemFromRight'>code</P>
                                <p class='itemFromRight'>22</P>
                                <p class='itemFromRight'>HTMLCSS3ES6-7-8NodeReact...</p>
                            </div>`
                    })
                }
                if (i === 450) {
                    if (document.getElementsByClassName('Scrool')[0]) {
                        document.getElementsByClassName('Scrool')[0].style.transform = 'translateX(-50%) translateY(-315px)'
                    }
                }
                if (i === 651) {
                    this.setState({ show: true })
                }
                this.setState({
                    text: this.state.text + start[i]
                })
            }, i * 100)
        }
    }
    toggleStatus() {
        if (this.refs._ref) {
            const el = document.getElementsByClassName('_topView2')[0]
            if (el){
            (/toggleStatus/.test(el.className)) ? (el.className = el.className.replace(/toggleStatus/, ' ')) : (el.className += ' toggleStatus ')
        }}

    }
    render() {
        return (
            <div className="aboutContainer">
                <div ref='ref' className="diyView contentSlideFromLeft">
                    {
                        this.state.show && <Icon onClick={() => this.toggleStatus()} className='togleStatus' type="redo" />
                    }
                    {
                        ReactHtmlParser(this.state.html)
                    }
                </div>
                <div className="inputText contentSlideFromRight">
                    <div className="Scrool">
                        {
                            this.state.text
                        }
                    </div>

                </div>
            </div>
        )
    }
}